import BannerWrapper from "./banner.style";

import xiaofeng_quote from "assets/images/deck/xiaofeng_quote.png"; 


const Banner = () => {
  return (
    <BannerWrapper>
      <Container>
        <Row>
          <Col className="lg-8 xs-12">
            <Box className="banner-content">
              {/*
              <Badge className="offer-text">
                14 days, a huge of free trial
              </Badge>
              */}
              <Heading>
                BLOCKCHAIN 3.0 <br/>
                Reimagine Internet
              </Heading>
              <Text as="span" className="description">
                In the ocean of a few thousand blockchains, why we still need ICP? <br />
                <br />
                Chairman of the influential Wanxiang Blockchain Group, Dr Xiao Feng explained this well
                in his speech "Breakthrough in Blockchain Ignites with Developers" on Oct 10, 2020. <br />
                <br />
                While Bitcoin has become the store of value and Ethereum is becoming the default protocol 
                to define asset ownership, ICP will become the blockchain network that runs web applications.
                <br />
                <br />
                ICP marks the next stage of blockchain evolution, or Blockchain 3.0. Everything that can be built
                on BTC, ETH or other blockchains, can be built on ICP as well, such as DeFi, NFT and ICO, at lower cost,
                faster speed and infinite capacity. 
                <br /><br />
                Many other things that are thriving on traditional Internet in Web2 era, such as Gaming, Social Network, 
                CRM, SaaS, e-Commerce, AI, Machine Learning, Big Data or IoT, are out of reach by conventional blockchains
                due to their limited scope or aspiration.
                <br /><br />
                They can all be built on ICP, which sets out to build not just another better version of Ethereum, but to reimagine a 
                much more robust, interoperatable and decentralized Internet all together.
                <br /><br /><br />
              </Text>
              {/*
              <Box className="banner-btn">
                <Link to="#" className="btn btn-fill">
                  {" "}
                  Get in touch{" "}
                </Link>
                <Link to="#" className="btn">
                  {" "}
                  white paper{" "}
                </Link>
              </Box>
              */}
            </Box>
          </Col>
          <Col className="lg-5 md-6 sm-12">
            <img src={xiaofeng_quote} alt="xiao feng quote" width="493.537"/>
          </Col>
        </Row>
        {/*
        <img
          src={BannerImage}
          className="banner__thumb"
          alt="cryptik banner thumb"
        />
        */}
      </Container>
    </BannerWrapper>
  )
}

export default Banner
